	module.exports = {"data":{"markdownRemark":{"html":"<p>何年もの間、習慣を身につけたり、夜の死人の中でその日の経験を静かに考える習慣を身につけなければなりません。日中は、忙しい一日で過ごす時間はほとんどありません。考えられる時間はありません。思う時間があるときには、それらの大半は深刻で退屈な学問的思考にも使われ、職業は非常に強力です。人生にとって、考える時間はありません。窓の外に点滅する街灯を見て、あなたは自分の仕事や学問の研究にないことを考えながら、自分自身と世界を静かに考えることができます。意味のあることを見つけ、少し考えてみてください。\nこのような考え方は人々に有益です。人は彼の専門的な活動の多くを行っています。もし彼が調整しなければ、彼は独身になり、彼の思考はゆっくりと向きが変わり、開かれた心はありません。したがって、強烈な脳や身体活動に加えて、思考のための新しい空間を発見してください。</p>\n<p>それが「政治的生活」と呼ばれた理由は政治的経験ではなく、むしろ政治学者として私の時間のほとんどを私の専門的な知識、さらにはこの知識に費やしたからです。私の人生のほとんどを占領する。このフィールドは非常に広範で、すべてのトピックは政治的観点から見ることができます。今日の世界と中国に住む人は誰もが政治家になれないと思う。\nなぜ彼らが特別な価値を持っているからではなく、彼らが静かな外と静かな内部状態で形成されているからです。平和と静けさ、これは追求する価値のある領域です。\n上海人民公会堂はそのような本を出版しようとしているので、私は夜の思いを記録します。もちろん、これはほんの一部です。</p>\n<p>1994年末の復旦大学</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0000.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0000.md","frontmatter":{"title":"自序"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0000.md absPath of file >>> MarkdownRemark"}}

